export default {
    created() {
        //计算滚动高度
        uni.getSystemInfo({
            success: function (res) {
                this.windowHeight=res.windowHeight;
            }
        });
        // 请求订单数据
        this.getData()
    },
    data() {
        return {
            orderList: [],
            // 滚动条高度
            windowHeight:0
        };
    },
    methods: {
        async getData() {
            await this.$u.get('/my_dy_task_order').then(res => {
                this.orderList = res.data
            })
        }
    }
}
